p,h1,h2,h3,img {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.leaflet-container {
  width: 100%;
  height: calc(100vh - 115px);
}

.leaflet-control-attribution {
  display: none;
}

/* Remove leaflet lines between tiles
https://github.com/Leaflet/Leaflet/issues/3575 */
.leaflet-tile-container img {
  width: 256.8px !important;
  height: 256.8px !important;
}

.vertex-icon {
  opacity: 0.0;
  background-image: url(images/bullseye-flat.svg);
  // filter: invert(27%) sepia(51%) saturate(2878%) hue-rotate(346deg) brightness(104%) contrast(97%);
  filter: invert(30%) sepia(40%) saturate(2878%);
  z-index: 20 !important;;

  -webkit-transition: opacity 0.3s ease-in-out;
  -moz-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}

.vertex-icon:hover {
  opacity: 1.0;
}

.exit-icon {
  opacity: 0.0;
  background-image: url(images/exit.svg);
  filter: invert(100%);
  z-index: 20 !important;;

  -webkit-transition: opacity 0.3s ease-in-out;
  -moz-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}

.exit-icon:hover {
  opacity: 1.0;
}

.MuiDialog-paperScrollPaper {
  background-color: ivory;
}

.banner {
  position: absolute;
  left: 50%;
  display: block;
  margin: 100px -200px;
  width: 400px;
  height: 60px;
  border: 1px solid #b92;
  font: normal 30px/60px 'MedievalSharp';
  text-align: center;
  color: black;
  background: #ca3;
  border-radius: 4px;
  box-shadow: 0 0 30px rgba(0,0,0,.15) inset,
      				0 6px 10px rgba(0,0,0,.15);
}

.banner::before,
.banner::after {
  content: '';
  position: absolute;
  z-index: -1;
  left: -70px;
  top: 24px;
  display: block;
  width: 40px;
  height: 0px;
  border: 30px solid #ca3;
  border-right: 20px solid #a82;
  border-bottom-color: #c8a42e;
  border-left-color: transparent;
  transform: rotate(-5deg);
}

.banner::after {
  left: auto;
  right: -70px;
  border-left: 20px solid #a82;
  border-right: 30px solid transparent;
  transform: rotate(5deg);
}

.title {
    height: 65px;
    padding: 20px 0;
}

.shadow {
  -webkit-filter: drop-shadow( 3px 3px 2px rgba(0, 0, 0, .7));
  filter: drop-shadow( 3px 3px 2px rgba(0, 0, 0, .7));
  /* Similar syntax to box-shadow */
}